	module.exports = {"data":{"postsJson":{"title":"Better than Angels, Moses & Joshua","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","display":"block","bookName":"Hebrews - ΠΡΟΣ ΕΒΡΑΙΟΥΣ","ref":"1:1-4:13","html":"<p>   <strong>1</strong><u>1</u>Πολυμερῶς καὶ πολυτρόπως πάλαι ὁ θεὸς λαλήσας τοῖς πατράσιν ἐν τοῖς προφήταις <u>2</u>ἐπʼ ἐσχάτου τῶν ἡμερῶν τούτων ἐλάλησεν ἡμῖν ἐν υἱῷ, ὃν ἔθηκεν κληρονόμον πάντων, διʼ οὗ καὶ ἐποίησεν τοὺς αἰῶνας· <br>   <u>3</u>ὃς ὢν ἀπαύγασμα τῆς δόξης καὶ χαρακτὴρ τῆς ὑποστάσεως αὐτοῦ,<br>   φέρων τε τὰ πάντα τῷ ῥήματι τῆς δυνάμεως αὐτοῦ, <br>   καθαρισμὸν τῶν ἁμαρτιῶν ποιησάμενος <br>   ἐκάθισεν ἐν δεξιᾷ τῆς μεγαλωσύνης ἐν ὑψηλοῖς, <br>   <u>4</u>τοσούτῳ κρείττων γενόμενος τῶν ἀγγέλων<br>   ὅσῳ διαφορώτερον παρʼ αὐτοὺς κεκληρονόμηκεν ὄνομα.<br>   <u>5</u>Τίνι γὰρ εἶπέν ποτε τῶν ἀγγέλων· <br>   υἱός μου εἶ σύ, <br>   ἐγὼ σήμερον γεγέννηκά σε; <br>   καὶ πάλιν· <br>   ἐγὼ ἔσομαι αὐτῷ εἰς πατέρα, <br>   καὶ αὐτὸς ἔσται μοι εἰς υἱόν; <br><u>6</u>ὅταν δὲ πάλιν εἰσαγάγῃ τὸν πρωτότοκον εἰς τὴν οἰκουμένην, λέγει· <br>   καὶ προσκυνησάτωσαν αὐτῷ πάντες ἄγγελοι θεοῦ. <br><u>7</u>καὶ πρὸς μὲν τοὺς ἀγγέλους λέγει· <br>   ὁ ποιῶν τοὺς ἀγγέλους αὐτοῦ πνεύματα <br>   καὶ τοὺς λειτουργοὺς αὐτοῦ πυρὸς φλόγα, <br><u>8</u>πρὸς δὲ τὸν υἱόν·<br>   ὁ θρόνος σου ὁ θεὸς εἰς τὸν αἰῶνα τοῦ αἰῶνος, <br>   καὶ ἡ ῥάβδος τῆς εὐθύτητος ῥάβδος τῆς βασιλείας σου. <br>   <u>9</u>ἠγάπησας δικαιοσύνην καὶ ἐμίσησας ἀνομίαν· <br>   διὰ τοῦτο ἔχρισέν σε ὁ θεὸς ὁ θεός σου <br>   ἔλαιον ἀγαλλιάσεως παρὰ τοὺς μετόχους σου. <br><u>10</u>καί· <br>   σὺ κατʼ ἀρχάς, κύριε, τὴν γῆν ἐθεμελίωσας, <br>   καὶ ἔργα τῶν χειρῶν σού εἰσιν οἱ οὐρανοί· <br>   <u>11</u>αὐτοὶ ἀπολοῦνται, σὺ δὲ διαμένεις, <br>   καὶ πάντες ὡς ἱμάτιον παλαιωθήσονται, <br>   <u>12</u>καὶ ὡσεὶ περιβόλαιον ἑλίξεις αὐτούς, <br>   ὡς ἱμάτιον καὶ ἀλλαγήσονται· <br>   σὺ δὲ ὁ αὐτὸς εἶ καὶ τὰ ἔτη σου οὐκ ἐκλείψουσιν. <br><u>13</u>πρὸς τίνα δὲ τῶν ἀγγέλων εἴρηκέν ποτε· <br>   κάθου ἐκ δεξιῶν μου, <br>   ἕως ἂν θῶ τοὺς ἐχθρούς σου ὑποπόδιον τῶν ποδῶν σου; <br><u>14</u>οὐχὶ πάντες εἰσὶν λειτουργικὰ πνεύματα εἰς διακονίαν ἀποστελλόμενα διὰ τοὺς μέλλοντας κληρονομεῖν σωτηρίαν; <br>   <strong>2</strong><u>1</u>Διὰ τοῦτο δεῖ περισσοτέρως προσέχειν ἡμᾶς τοῖς ἀκουσθεῖσιν, μήποτε παραρυῶμεν. <u>2</u>εἰ γὰρ ὁ διʼ ἀγγέλων λαληθεὶς λόγος ἐγένετο βέβαιος καὶ πᾶσα παράβασις καὶ παρακοὴ ἔλαβεν ἔνδικον μισθαποδοσίαν, <u>3</u>πῶς ἡμεῖς ἐκφευξόμεθα τηλικαύτης ἀμελήσαντες σωτηρίας, ἥτις ἀρχὴν λαβοῦσα λαλεῖσθαι διὰ τοῦ κυρίου ὑπὸ τῶν ἀκουσάντων εἰς ἡμᾶς ἐβεβαιώθη, <u>4</u>συνεπιμαρτυροῦντος τοῦ θεοῦ σημείοις τε καὶ τέρασιν καὶ ποικίλαις δυνάμεσιν καὶ πνεύματος ἁγίου μερισμοῖς κατὰ τὴν αὐτοῦ θέλησιν; <br>   <u>5</u>Οὐ γὰρ ἀγγέλοις ὑπέταξεν τὴν οἰκουμένην τὴν μέλλουσαν, περὶ ἧς λαλοῦμεν. <u>6</u>διεμαρτύρατο δέ πού τις λέγων·<br>   τί ἐστιν ἄνθρωπος ὅτι μιμνῄσκῃ αὐτοῦ, <br>   ἢ υἱὸς ἀνθρώπου ὅτι ἐπισκέπτῃ αὐτόν; <br>   <u>7</u>ἠλάττωσας αὐτὸν βραχύ τι παρʼ ἀγγέλους, <br>   δόξῃ καὶ τιμῇ ἐστεφάνωσας αὐτόν, <br>   <u>8</u>πάντα ὑπέταξας ὑποκάτω τῶν ποδῶν αὐτοῦ. <br>   ἐν τῷ γὰρ ὑποτάξαι αὐτῷ τὰ πάντα οὐδὲν ἀφῆκεν αὐτῷ ἀνυπότακτον. Νῦν δὲ οὔπω ὁρῶμεν αὐτῷ τὰ πάντα ὑποτεταγμένα· <u>9</u>τὸν δὲ βραχύ τι παρʼ ἀγγέλους ἠλαττωμένον βλέπομεν Ἰησοῦν διὰ τὸ πάθημα τοῦ θανάτου δόξῃ καὶ τιμῇ ἐστεφανωμένον, ὅπως χάριτι θεοῦ ὑπὲρ παντὸς γεύσηται θανάτου. <br>   <u>10</u>Ἔπρεπεν γὰρ αὐτῷ, διʼ ὃν τὰ πάντα καὶ διʼ οὗ τὰ πάντα, πολλοὺς υἱοὺς εἰς δόξαν ἀγαγόντα τὸν ἀρχηγὸν τῆς σωτηρίας αὐτῶν διὰ παθημάτων τελειῶσαι. <u>11</u>ὅ τε γὰρ ἁγιάζων καὶ οἱ ἁγιαζόμενοι ἐξ ἑνὸς πάντες· διʼ ἣν αἰτίαν οὐκ ἐπαισχύνεται ἀδελφοὺς αὐτοὺς καλεῖν <u>12</u>λέγων· <br>   ἀπαγγελῶ τὸ ὄνομά σου τοῖς ἀδελφοῖς μου, <br>   ἐν μέσῳ ἐκκλησίας ὑμνήσω σε, <br><u>13</u>καὶ πάλιν·<br>   ἐγὼ ἔσομαι πεποιθὼς ἐπʼ αὐτῷ, <br>   καὶ πάλιν· <br>   ἰδοὺ ἐγὼ καὶ τὰ παιδία ἅ μοι ἔδωκεν ὁ θεός. <br><u>14</u>Ἐπεὶ οὖν τὰ παιδία κεκοινώνηκεν αἵματος καὶ σαρκός, καὶ αὐτὸς παραπλησίως μετέσχεν τῶν αὐτῶν, ἵνα διὰ τοῦ θανάτου καταργήσῃ τὸν τὸ κράτος ἔχοντα τοῦ θανάτου, τοῦτʼ ἔστιν τὸν διάβολον, <u>15</u>καὶ ἀπαλλάξῃ τούτους, ὅσοι φόβῳ θανάτου διὰ παντὸς τοῦ ζῆν ἔνοχοι ἦσαν δουλείας. <u>16</u>οὐ γὰρ δήπου ἀγγέλων ἐπιλαμβάνεται ἀλλὰ σπέρματος Ἀβραὰμ ἐπιλαμβάνεται. <u>17</u>ὅθεν ὤφειλεν κατὰ πάντα τοῖς ἀδελφοῖς ὁμοιωθῆναι, ἵνα ἐλεήμων γένηται καὶ πιστὸς ἀρχιερεὺς τὰ πρὸς τὸν θεὸν εἰς τὸ ἱλάσκεσθαι τὰς ἁμαρτίας τοῦ λαοῦ. <u>18</u>ἐν ᾧ γὰρ πέπονθεν αὐτὸς πειρασθείς, δύναται τοῖς πειραζομένοις βοηθῆσαι. <br>   <strong>3</strong><u>1</u>Ὅθεν, ἀδελφοὶ ἅγιοι, κλήσεως ἐπουρανίου μέτοχοι, κατανοήσατε τὸν ἀπόστολον καὶ ἀρχιερέα τῆς ὁμολογίας ἡμῶν Ἰησοῦν, <u>2</u>πιστὸν ὄντα τῷ ποιήσαντι αὐτὸν ὡς καὶ Μωϋσῆς ἐν ὅλῳ τῷ οἴκῳ αὐτοῦ. <u>3</u>πλείονος γὰρ οὗτος δόξης παρὰ Μωϋσῆν ἠξίωται, καθʼ ὅσον πλείονα τιμὴν ἔχει τοῦ οἴκου ὁ κατασκευάσας αὐτόν· <u>4</u>πᾶς γὰρ οἶκος κατασκευάζεται ὑπό τινος, ὁ δὲ πάντα κατασκευάσας θεός. <u>5</u>καὶ Μωϋσῆς μὲν πιστὸς ἐν ὅλῳ τῷ οἴκῳ αὐτοῦ ὡς θεράπων εἰς μαρτύριον τῶν λαληθησομένων, <u>6</u>Χριστὸς δὲ ὡς υἱὸς ἐπὶ τὸν οἶκον αὐτοῦ· οὗ οἶκός ἐσμεν ἡμεῖς, ἐάνπερ τὴν παρρησίαν καὶ τὸ καύχημα τῆς ἐλπίδος κατάσχωμεν. <br>   <u>7</u>Διό, καθὼς λέγει τὸ πνεῦμα τὸ ἅγιον· <br>   σήμερον ἐὰν τῆς φωνῆς αὐτοῦ ἀκούσητε, <br>   <u>8</u>μὴ σκληρύνητε τὰς καρδίας ὑμῶν ὡς ἐν τῷ παραπικρασμῷ <br>   κατὰ τὴν ἡμέραν τοῦ πειρασμοῦ ἐν τῇ ἐρήμῳ, <br>   <u>9</u>οὗ ἐπείρασαν οἱ πατέρες ὑμῶν ἐν δοκιμασίᾳ <br>   καὶ εἶδον τὰ ἔργα μου <u>10</u>τεσσεράκοντα ἔτη· <br>   διὸ προσώχθισα τῇ γενεᾷ ταύτῃ <br>   καὶ εἶπον· ἀεὶ πλανῶνται τῇ καρδίᾳ, <br>   αὐτοὶ δὲ οὐκ ἔγνωσαν τὰς ὁδούς μου, <br>   <u>11</u>ὡς ὤμοσα ἐν τῇ ὀργῇ μου· <br>   εἰ εἰσελεύσονται εἰς τὴν κατάπαυσίν μου. <br><u>12</u>Βλέπετε, ἀδελφοί, μήποτε ἔσται ἔν τινι ὑμῶν καρδία πονηρὰ ἀπιστίας ἐν τῷ ἀποστῆναι ἀπὸ θεοῦ ζῶντος, <u>13</u>ἀλλὰ παρακαλεῖτε ἑαυτοὺς καθʼ ἑκάστην ἡμέραν, ἄχρις οὗ τὸ σήμερον καλεῖται, ἵνα μὴ σκληρυνθῇ τις ἐξ ὑμῶν ἀπάτῃ τῆς ἁμαρτίας — <u>14</u>μέτοχοι γὰρ τοῦ Χριστοῦ γεγόναμεν, ἐάνπερ τὴν ἀρχὴν τῆς ὑποστάσεως μέχρι τέλους βεβαίαν κατάσχωμεν — <u>15</u>ἐν τῷ λέγεσθαι· <br>   σήμερον ἐὰν τῆς φωνῆς αὐτοῦ ἀκούσητε, <br>   μὴ σκληρύνητε τὰς καρδίας ὑμῶν ὡς ἐν τῷ παραπικρασμῷ. <br><u>16</u>τίνες γὰρ ἀκούσαντες παρεπίκραναν˸; ἀλλʼ οὐ πάντες οἱ ἐξελθόντες ἐξ Αἰγύπτου διὰ Μωϋσέως; <u>17</u>τίσιν δὲ προσώχθισεν τεσσεράκοντα ἔτη; οὐχὶ τοῖς ἁμαρτήσασιν, ὧν τὰ κῶλα ἔπεσεν ἐν τῇ ἐρήμῳ; <u>18</u>τίσιν δὲ ὤμοσεν μὴ εἰσελεύσεσθαι εἰς τὴν κατάπαυσιν αὐτοῦ εἰ μὴ τοῖς ἀπειθήσασιν; <u>19</u>καὶ βλέπομεν ὅτι οὐκ ἠδυνήθησαν εἰσελθεῖν διʼ ἀπιστίαν. <br>   <strong>4</strong><u>1</u>Φοβηθῶμεν οὖν, μήποτε καταλειπομένης ἐπαγγελίας εἰσελθεῖν εἰς τὴν κατάπαυσιν αὐτοῦ δοκῇ τις ἐξ ὑμῶν ὑστερηκέναι. <u>2</u>καὶ γάρ ἐσμεν εὐηγγελισμένοι καθάπερ κἀκεῖνοι· ἀλλʼ οὐκ ὠφέλησεν ὁ λόγος τῆς ἀκοῆς ἐκείνους μὴ συγκεκερασμένους τῇ πίστει τοῖς ἀκούσασιν. <u>3</u>Εἰσερχόμεθα γὰρ εἰς τὴν κατάπαυσιν οἱ πιστεύσαντες, καθὼς εἴρηκεν· <br>   ὡς ὤμοσα ἐν τῇ ὀργῇ μου· <br>   εἰ εἰσελεύσονται εἰς τὴν κατάπαυσίν μου, <br>   καίτοι τῶν ἔργων ἀπὸ καταβολῆς κόσμου γενηθέντων. <u>4</u>εἴρηκεν γάρ που περὶ τῆς ἑβδόμης οὕτως· καὶ κατέπαυσεν ὁ θεὸς ἐν τῇ ἡμέρᾳ τῇ ἑβδόμῃ ἀπὸ πάντων τῶν ἔργων αὐτοῦ, <u>5</u>καὶ ἐν τούτῳ πάλιν· εἰ εἰσελεύσονται εἰς τὴν κατάπαυσίν μου. <u>6</u>ἐπεὶ οὖν ἀπολείπεται τινὰς εἰσελθεῖν εἰς αὐτήν, καὶ οἱ πρότερον εὐαγγελισθέντες οὐκ εἰσῆλθον διʼ ἀπείθειαν, <u>7</u>πάλιν τινὰ ὁρίζει ἡμέραν, σήμερον, ἐν Δαυὶδ λέγων μετὰ τοσοῦτον χρόνον, καθὼς προείρηται· <br>   σήμερον ἐὰν τῆς φωνῆς αὐτοῦ ἀκούσητε, <br>   μὴ σκληρύνητε τὰς καρδίας ὑμῶν. <br><u>8</u>εἰ γὰρ αὐτοὺς Ἰησοῦς κατέπαυσεν, οὐκ ἂν περὶ ἄλλης ἐλάλει μετὰ ταῦτα ἡμέρας. <u>9</u>ἄρα ἀπολείπεται σαββατισμὸς τῷ λαῷ τοῦ θεοῦ. <u>10</u>ὁ γὰρ εἰσελθὼν εἰς τὴν κατάπαυσιν αὐτοῦ καὶ αὐτὸς κατέπαυσεν ἀπὸ τῶν ἔργων αὐτοῦ ὥσπερ ἀπὸ τῶν ἰδίων ὁ θεός. <u>11</u>Σπουδάσωμεν οὖν εἰσελθεῖν εἰς ἐκείνην τὴν κατάπαυσιν, ἵνα μὴ ἐν τῷ αὐτῷ τις ὑποδείγματι πέσῃ τῆς ἀπειθείας. <u>12</u>Ζῶν γὰρ ὁ λόγος τοῦ θεοῦ καὶ ἐνεργὴς καὶ τομώτερος ὑπὲρ πᾶσαν μάχαιραν δίστομον καὶ διϊκνούμενος ἄχρι μερισμοῦ ψυχῆς καὶ πνεύματος, ἁρμῶν τε καὶ μυελῶν, καὶ κριτικὸς ἐνθυμήσεων καὶ ἐννοιῶν καρδίας· <u>13</u>καὶ οὐκ ἔστιν κτίσις ἀφανὴς ἐνώπιον αὐτοῦ, πάντα δὲ γυμνὰ καὶ τετραχηλισμένα τοῖς ὀφθαλμοῖς αὐτοῦ, πρὸς ὃν ἡμῖν ὁ λόγος. </p>","path":"/405","Question1":"List all the many questions raised in this passage. Who makes them? To whom are they made?","Question2":"Is there a question that impacts you more than others? Why?","Question3":"What questions would do you have about issues raised in this passage?","Question4":"What can you do to get some answers to these questions you have raised?","prevPath":"/404","nextPath":"/406"}},"pathContext":{}}

